import interfaceurl from '@/utils/interface.js'
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: '我的订单' //导航栏文字
			},
			tabIndex: 0, //订单状态索引
			tabList: [{
				text: '全部',
				val: ''
			},{
				text: '待发货',
				val: '20'
			},{
				text: '待收货',
				val: '30'
			},{
				text: '售后',
				val: '70,80'
			},{
				text: '已完成',
				val: '40'
			}],
			params: {
				page: 1, //页数
				size: 3, //每页几条
				status: ''
			}, //分页参数
			orderPageData: {}, //订单数据
			orderPageList: [], //订单列表
			orderCount: [] //每个订单的商品个数
		}
	},
	onLoad(options) {
		
	},
	onShow() {
		this.initData()
	},
	onReachBottom() {
		if (this.orderPageList.length >= this.orderPageData.total) {
			return
		}
		this.params.page++;
		this.getOrderPageList()
	},
	methods: {
		initData() {
			//重置分页参数
			this.orderPageData = {}
			this.orderPageList = []
			this.params.page = 1
			this.getOrderPageList();
		},
		getOrderPageList() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.orderPageList, this.params).then((res) => {
				that.orderPageData = res.data
				if(that.params.page == 1) {
					that.orderPageList = res.data.data
				} else {
					that.orderPageList = that.orderPageList.concat(res.data.data)
				}
				that.orderCount = []
				let num = 0;
				for(var item of that.orderPageList) {
					for(var subItem of item.cartList) {
						num += subItem.quantity
						item.status = this.setStatus(item.status)
					}
					that.orderCount.push(num)
					num = 0;
				}
			});
		},
		setStatus(status) {
			if(status == 0) {
				return '待支付'
			}
		},
		selectTab(index, status) {
			this.tabIndex = index
			this.params.status = status
			this.initData();
		}
	}
}
